.phone-number {
    @include flex-all-center;
    cursor: pointer;
    &:hover {
        .phone-number__backgroundhide {
            opacity: 1;
        }
        .phone-number__background {
            opacity: 0;
        }
    }
    &__backgroundhide, &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: .3s all ease 0s;
        @include tablet {
            display: none;
        }
    }
    // .header-number__backgroundhide

    &__backgroundhide {
      opacity: 0;
      z-index: -49;
     
    }
    // .header-number__background

    &__background {
       z-index: -50;
    }

    // .header-number__content

    &__content {
        position: relative;
        z-index: 5;
       display: flex;
       align-items: center;
    }

    // .header-number__ico

    &__ico {
        width: 25px;
        height: 25px;
        margin: 0 8px 0 0;
        @media (max-width: 350px) {
            display: none;
        }
    }

    // .header-number__text

    &__text {
        
        text-transform: uppercase;
        color: #fff;
        font-size: 25px;
        font-weight: 700;
        font-family: $heliosFont;
        @include mobile2 {
            font-size: 20px;
        }
    }
}

