@import "mixins";


.history {  
    width: 100%;
    min-height: 100vh;
    padding: 104px 0 100px 0;
    @include container {
        padding: 124px 0 100px 0;
    }
    @include tablet2 {
        padding: 90px 0 0 0;
    }
    // .history__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .history__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .history__top

    &__top {
        width: 100%;
        @include flex-all-sb;
        font-family: $lucindaFont;
        margin: 0 0 90px 0;
        @include container {
            flex-direction: column;
            margin: 0 0 50px 0;
        }
    }

    // .history__title

    &__title {
        color: $darkColor;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 80px;
        max-width: 565px;
        @include container {
            max-width: 100%;
            margin: 0 0 30px 0;
            text-align: center;
        }
        @include tablet2 {
            font-size: 50px;
            margin: 0 0 10px 0;
        }
    }

    // .history__block

    &__block {
        width: 60%;
        background-color: $colorGrey;
        height: 336px;
        padding: 92px 0 76px 106px;
        position: relative;
        @include container {
           width: 100%;
           height: auto;
           text-align: center;
           padding: 30px 50px;
        }
        @include mobile {
            padding: 30px 20px;
        }
        &::before, &::after {
            position: absolute;
            top: 0;
            width: 100vw;
            height: 100%;
            content: "";
            background-color: $colorGrey;
            z-index: -1;
        }
        &::before {
            right: -100vw;
        }
        &::after {
            @include container {
                display: block;
                left: -100vw;
            }
            display: none;
        }
    }

    // .history__text

    &__text {
        color: #fff;
        font-size: 30px;
        @include mobile {
            font-size: 15px;
        }
    }

    // .history__main

    &__main {
       
    }
}


.history-main {
    width: 100%;
    @include container {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;

    }
    // .history-main__top

    &__top {
        width: 100%;
        position: relative;
        display: flex;
        justify-content: space-between;
        padding-left: 130px;
        margin: 0 0 55px 0;
        
        @include container {
            margin: 0;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border-left: 5px solid $colorRed;
            padding: 0;
            width: 600px;
            @include mobile {
                width: 100%;
            }
        }
        &::before {
            display: block;
            position: absolute;
            bottom: -40px;
            left: 0;
            content: "";
            width: 170px;
            height: 180px;
            background: url("../img/history/left.png") 0 0 no-repeat;
            @include container {
                display: none;
            }
        }
        &::after {
            display: block;
            position: absolute;
            bottom: -34px;
            left: 105px;
            content: "";
            width: 1260px;
            height: 4px;
            background: url("../img/history/long-line.png") 0 0 no-repeat;
            @include container {
                display: none;
            }
        }
    }

    // .history-main__block

    &__block {
        @include flex-v-center;
        @include container {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .history-main__item

    &__item {
        margin: 0 140px 0 0;
        position: relative;
        @include container {
            margin: 0;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        @include container {
           padding: 0 5px 50px 5px;

           @include mobile {
            padding: 0 5px 20px 5px;

        }
         }
    }

    // .history-main__ico

    &__ico {
        width: 93px;
        height: 93px;
        margin: 0 0 25px 0;
        @include container {
            width: 120px;
            height: 120px;
        }
        @include mobile {
            width: 73px;
            height: 73px;
        }
    }

    // .history-main__year

    &__year {
        margin: 0 0 30px 0;
        color: $darkColor;
        font-weight: 700;
        font-family: $heliosFont;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        position: relative;
        font-size: 40px; 
        @include container {
            font-size: 50px;
            margin: 0 0 10px 0;
        }
        @include mobile {
            font-size: 40px; 
        }
       
    }

    // .history-main__text

    &__text {
        max-width: 263px;
        color: $colorGrey;
        font-size: 20px;
        font-family: $lucindaFont;
        @include container {
           font-size: 25px;
           max-width: 350px;
           text-align: center;
        }
        @include mobile2 {
           font-size: 15px;

        }
    }

    // .history-main__future

    &__future {
        color: $colorRed;
        font-weight: 700;
        font-family: $heliosFont;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        font-size: 40px;
        margin-top: 120px;
        @include mobile {
            margin-top: 40px;
            font-size: 20px; 
        }
    }

    &__bottom {
        width: 100%;
        position: relative;
        display: flex;
        padding-left: 130px;
        @include container {
            margin: 0;
            flex-direction: column;
            justify-content: center;
            padding: 0;
            width: 600px;
            @include mobile {
                width: 100%;
            }
            border-left: 5px solid $colorRed;
        }
        &::after {
            display: block;
            position: absolute;
            top: -25px;
            right: -120px;
            content: "";
            width: 170px;
            height: 180px;
            background: url("../img/history/right.png") 0 0 no-repeat;
            @include container {
                display: none;
            }
        }
    }
}

