@import "mixins";


.project {
    width: 100%;
    min-height: 100vh;
    position: relative;
    padding: 190px 0 30px 0;
    @include tablet2 {
        padding: 110px 0 30px 0;
    }
    // .project__illustration

    &__illustration {
        position: absolute;
        right: 0;
        top: 190px;
        z-index: -1;
        @include container {
            display: none;
        }
    }

    // .project__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .project__body

    &__body {
        width: 100%;
        height: 100%; 
        color: $darkColor;
    }

    // .project__title

    &__title {
        font-family: $lucindaFont;
        margin: 0 0 25px 0; 
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 45px;
        @include mobile {
            font-size: 35px;
            margin: 0 0 15px 0; 
         }
    }

    // .project__text

    &__text {
        font-family: $lucindaFont;
        max-width: 1195px;
        color: $colorGrey;
        font-size: 15px;
        margin: 0 0 20px 0;
        @include container {
            max-width: 900px;
        }
        @include mobile {
           font-size: 14px;
        }
       
    }

    // .project__block

    &__block {
        margin: 0 0 30px 0;
        display: flex;
        @include tablet2 {
            flex-direction: column;
        }

    }

    // .project__image

    &__image {
        border-radius: 8px;
        max-width: 700px;
        height: 423px;
        margin: 0 28px 0 0;
        @include container {
            max-width: 500px;
        }
        @include tablet2 {
            max-width: 100%;
            margin: 0 0 30px 0;
            height: auto;
        }
    }

    // .project__content

    &__content {
    }

    // .project__item

    &__item {
        margin: 0 0 35px 0;
        @include mobile {
            margin: 0 0 20px 0;
        }
    }

    // .project__caption

    &__caption {
        font-family: $heliosFont;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 25px;
        font-weight: 700; 
        margin: 0 0 20px 0;
        @include mobile {
            font-size: 22px;
            margin: 0 0 10px 0;
        }
    }

    // .project__description

    &__description {
        font-family: $lucindaFont;
        color: $colorGrey;
        max-width: 460px;
        font-size: 15px;
        @include tablet2 {
           max-width: 700px;
        }
    }

    // .project__list

    &__list {
        img {
            width: 20px;
            height: 20px;
            margin: 0 5px 0 0;
        }
        a {
            font-family: $lucindaFont;
            color: $colorRed;
            font-size: 20px;
            transition: .3s all ease 0s;
            &:hover {
                border-bottom: 1px solid $colorRed;
            }
        }
    }

    // .project__button

    &__button {
        width: 252px;
    }
}
