@import "mixins";


.projects {
    width: 100%;
    background-color: $darkColor;
    padding: 74px 0 110px 0;
    @include mobile {
        padding: 44px 0 50px 0;
    }
    // .projects__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .projects__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .projects__header

    &__header {
        width: 100%;
        font-family: $lucindaFont;
        @include flex-all-sb;
        margin: 0 0 40px 0;
        @include container {
            display: block;
        }
        @include mobile {
            margin: 0 0 20px 0;
        }
    }

    // .projects__title

    &__title {
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 80px;
        @include mobile {
            font-size: 40px;
        }
    }

    // .projects__subtitle

    &__subtitle {
        position: relative;
        font-size: 30px;
        @include mobile {
            font-size: 20px;
        }
        &::before {
            position: absolute;
            content: "";
            top: 15px;
            left: -50px;
            background-color: #fff;
            width: 33px;
            height: 3px;
            @include container {
                display: none;
            }
        }
    }

		// .projects__content

		&__content {
            width: 100%;
            @include flex-all-sb;
            flex-wrap: wrap;
		}

		// .projects__item

		&__item {
            position: relative;
            width: 24%;
            min-height: 358px;
            border-radius: 8px;
            cursor: pointer;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 20px;
            @include mobile {
                padding: 10px;
            }
            @include tablet {
                width: 100%;
                margin: 0 0 30px 0;
                animation-name: none;
            }
            &:first-child, &:nth-child(2), &:nth-child(3) {
                margin: 0 0 30px 0;
            }
            &:first-child, &:nth-child(5) {
                width: 49%;
                .projects__text {
                    display: block;
                }
                .projects__button {
                    width: 323px;
                    @include mobile2 {
                        width: 226px;
                    }
                }
                @include tablet {
                    width: 100%;
                    margin: 0 0 30px 0;
                }
            }
            &::before {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 100%;
                content: "";
                z-index: 2;
                background-image: url("../img/projects/shadow.png");
               opacity: 0;
                transition: .5s all ease 0s;
                background-size: cover;
            }
            &:hover {
                align-items: center;
                justify-content: center;
               
                &::before {
                    opacity: 1;
                }
                .projects__button {
                    display: block;
                }
                .projects__text {
                    display: block;
                    text-align: center;
                    margin: 0 0 30px 0;
                }
                .projects__caption {
                    margin: 0 0 25px 0;
                    text-align: center;
                }
            }
           
		}

		// .projects__backgroundimage

		&__backgroundimage {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 8px;
		}

		// .projects__caption

		&__caption {
            z-index: 3;
            position: relative;
            font-family: $heliosFont;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            font-size: 30px;
            font-weight: 700;
            @include tablet {
                margin: 0 0 10px 0;
             }
             @include mobile {
                 font-size: 20px;
             }
		}

		// .projects__text

		&__text {
            z-index: 3;
            position: relative;
            max-width: 318px;
            font-family: $lucindaFont;
            display: none;
            font-size: 20px;
            @include mobile {
                font-size: 15px;
            }
            @include tablet {
               display: block;
            }
            &:first-child, &:nth-child(5) {
               display: block;
           }
		}

		// .projects__button

		&__button {
            z-index: 3;
            position: relative;
            width: 226px;
            display: none;
            @include tablet {
                width: 323px;
            }
            @include mobile2 {
                width: 226px;
            }
		}

}
