@import "mixins";


.comments {
    width: 100%;
    position: relative;
    @include container {
        padding: 50px 0;
    }
    &::before {
        position: absolute;
        content: "";
        background-color: $darkColor;
        width: 40%;
        height: 100%;
        z-index: -1;
        @include container {
            display: none;
        }
    }
    // .comments__illustration

    &__illustration {
        position: absolute;
        bottom: 0;
        right: 0;
       @media (max-width: 1700px) {
           display: none;
       }
    }

    // .comments__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
        position: relative;
        &::before {
            @media (max-width: 1800px) {
                display: none;
            }
            position: absolute;
            content: "";
            left: -180px;
            bottom: 120px;
            width: 280px;
            height: 340px;
            z-index: 2;
            background-image: url("../img/common/illustration2.png");
        }
    }

    // .comments__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @include container {
           flex-direction: column;
        }
    }

    // .comments__left

	&__left {
        padding: 110px 0 90px 0;
        background-color: $darkColor;
        width: 50%;
        @include container {
           width: 100%;
           margin: 0 0 40px 0;
           padding: 0;
           background-color: transparent;
         }
	}

    // .comments__image

    &__image {
        height: 620px;
        object-fit: cover;
        border-bottom-left-radius: 8px;
        border-top-left-radius: 8px;
       z-index: 5;
       position: relative;
       @include container {
        width: 100%;
        border-radius: 8px;
      }
    }

    // .comments__content

    &__content {
        color: $darkColor;
        padding-left: 55px;
        @include container {
            width: 100%;
            padding: 0;
          
        }
    }

    // .comments__title

    &__title {
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 700;
        font-family: $heliosFont;
        margin: 0 0 30px 0;
        @include mobile {
            font-size: 35px;
            margin: 0 0 10px 0;
        }
    }

    // .comments__subtitle

    &__subtitle {
        position: relative;
        font-family: $lucindaFont;
        padding-left: 46px;
        font-size: 30px;
        margin: 0 0 45px 0;
        @include mobile {
            padding: 0;
            margin: 0 0 20px 0;
         }
        &::before {
            position: absolute;
            top: 17px;
            left: 0;
            content: "";
            background-color: $colorGrey;
            width: 33px;
            height: 3px;
            @include mobile {
               display: none;
            }
        }
    }

    // .comments__slider

    &__slider {
        width: 705px;
        min-height: 420px;
        @include container {
            width: 100%  ;
            
        }
      @media (max-width: 370px) {
        min-height: 470px;
      }
    }
}




.comments-slider {
    
    // .comments-slider__item

    &__item {
        font-family: $lucindaFont;
    }

    // .comments-slider__caption

    &__caption {
        color: $colorRed;
        margin: 0 0 22px 0;
       
        font-size: 25px;
        @include mobile {
            margin: 0 0 12px 0;
            font-size: 20px;
        }
    }

    // .comments-slider__text

    &__text {
        color: $colorGrey;
        font-size: 15px;
        @include mobile {
           font-size: 13px;
         }
         @media (max-width: 340px) {
            font-size: 12px;
        }
    }
}

.comments-slider .slick-arrow{
    position: absolute;
    bottom: 0;
    z-index: 10;
}
.comments-slider .slick-arrow.slick-prev{
    display: none;

    visibility: hidden;
}
.comments-slider .slick-arrow.slick-next{
    right: 0;
    font-size: 0;
    width: 47px;
    height: 47px;
    background: url("../img/arrow-next.svg") 0 0 no-repeat;
    border: none;
    cursor: pointer;
    animation: arrow-next-move 1s infinite ease;
}
@keyframes arrow-next-move  {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(10px);
    } 
    100% {
        transform: translateY(0);
    }
}

.comments-slider .slick-dots{
    position: absolute;
    bottom: 0;
    left: 0;
    @include flex-v-center;
}
.comments-slider .slick-dots li{
    list-style: none;
    margin: 0 16px 0 0;
}
.comments-slider .slick-dots li button{
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 2px solid rgba(84, 85, 85, 0.5);
    border-radius: 50%;
    font-size: 0;
    cursor: pointer;

}
.comments-slider .slick-dots li.slick-active button{
    border: 2px solid $colorRed;
    background-color: $colorRed;
}