@import "mixins";


.consulting {
    width: 100%;
    position: relative;
    padding: 80px 0 100px 0;
    z-index: 2;
    @include container {
        padding: 80px 0 80px 0;
    }
    @include mobile {
        padding: 40px 0 40px 0;
    }
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        background-color: $darkColor;
        width: 50%;
        height: 100%;
        content: " ";
        z-index: -2;
        border-bottom-right-radius: 8px;
        border-top-right-radius: 8px;
        @include container {
            width: 100%;
        }
    }
    // .consulting__line

    &__line {
        position: absolute;
        top: 0;
        right: 0;
        @include container {
           display: none;
            
        }
    }

    // .consulting__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .consulting__body

    &__body {
        width: 100%;
        height: 100%;
        
    }

    // .consulting__top

    &__top {
        width: 100%;
        @include flex-all-sb;
        @include container {
            display: block;
        }
    }

    // .consulting__title

    &__title {
        font-family: $lucindaFont;
        letter-spacing: 0.05em;
    text-transform: uppercase;
    font-size: 80px;
    color: #fff;
    @include mobile {
        font-size: 50px;
    }
    @include mobile2 {
        font-size: 32px;
    }
        span {
            color: $colorRed;
            letter-spacing: 0.05em;
        text-transform: uppercase;
       
        }
    }

    // .consulting__subtitle

    &__subtitle {
        position: relative;
        font-family: $lucindaFont;
        padding-left: 46px;
        font-size: 30px;
        @include container {
           color: #fff;
           padding: 0;
         }
         @include mobile {
            font-size: 25px;
        }
        &::before {
            position: absolute;
            top: 17px;
            left: 0;
            content: "";
            background-color: $colorGrey;
            width: 33px;
            height: 3px;
            @include container {
               display: none;
              }
        }
    }

    // .consulting__main

    &__main {
        width: 100%;
        @include flex-all-sb;
        @include container {
            display: block;
        }
    }

    // .consulting__items

    &__items {
        width: 50%;
        margin-top: 65px;
        @include container {
            margin: 50px 0 50px 0;
            width: 100%;
        }
        @include mobile {
            margin: 30px 0 30px 0;
        }
    }

    // .consulting__item

    &__item {
        @include flex-v-center;
        position: relative;
        height: 110px;
        @include container {
            width: 100%;
           
        }
        &_img1 {
            &::after {
                position: absolute;
                bottom:  18px;
                left: 0;
                content: "";
                width: 30px;
                height: 29px;
                background-image: url("../img/consulting/x.png");
                @include container {
                   display: none;
                  }
            }
        }
        &_img2 {
            &::after {
                position: absolute;
                top: 24px;
                left: -170px;
                content: "";
               width: 112px;
               height: 33px;
                background-image: url("../img/consulting/lines.png");
               @media (max-width: 1800px) {
                   display: none;
               }
            }
        }
        // .consulting__item_white

        &_white {
            background-color: #FFF;
            padding-left: 115px;
            @include mobile {
               padding: 0;
               animation-name: none;
            }
            &::before {
                position: absolute;
                top: 0;
                left: -100vw;
                content: "";
                width: 100vw;
                height: 100%;
                background-color: #fff;
                z-index: -1;
            }
            
        }
    }

    // .consulting__image

    &__image {
        width: 88px;
        height: 88px;
        margin: 0 20px 0 0;
        @include mobile2 {
            width: 48px;
            height: 48px;
            margin: 0 15px 0 0;
        }
    }


    // .consulting__block

    &__block {
        color: #fff;
        &_white {
            color: $darkColor;
        }
    }

    // .consulting__caption

    &__caption {
        font-family: $lucindaFont;
        margin: 0 0 15px 0;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 25px;
        font-weight: 700;
        @include mobile2 {
            font-size: 15px;
            margin: 0 0 5px 0;
        }
    }

    // .consulting__text

    &__text {
        font-family: $heliosFont;
        max-width: 233px;
        font-size: 15px;
        line-height: 140%;
        @include mobile2 {
            font-size: 12px;
        }
        &_white {
            color: $colorGrey;
        }
    }

    // .consulting__video

    &__video {
        position: relative;
        &::before {
            position: absolute;
            bottom: -60px;
            right: -40px;
            background-image: url("../img/consulting/cube.png");
            width: 110px;
            height: 110px;
            content: "";
            @include container {
                display: none;
            }
        }
        video {
            width: 930px;
            max-height: 550px;
            border-radius: 8px;
            @include container {
                width: 100%;
                height: auto;
                max-width: 100%;
                object-fit: cover;
            }
        }
      
    }
}
